//页面路由
import Vue from 'vue'
import VueRouter from 'vue-router'
import NProgress from 'nprogress';

import { canGoPath } from "./const";
import store from './store'
import NOTFOUND_PAGE from "../views/404.vue"
import INDEX_PAGE from "../views/index.vue"
import LOGIN_PAGE from "../views/login.vue"

Vue.use(VueRouter)

const routes = [
	{
		path: '/',
		redirect: '/index'
	},
	{
		path: '/index',
		component: INDEX_PAGE
	},
	{
		path: '/login',
		component: LOGIN_PAGE
	},
	{
		path: "/404",
        component: NOTFOUND_PAGE
	},
	{
		path: "*",
		redirect: { path: '/index' }
	}
]

const router = new VueRouter({
	routes
});

router.beforeEach((to, from, next) => {
	NProgress.start();
	store.commit("init");
	if (canGoPath.indexOf(to.path) != -1 && store.getters.isLogin) {
		next();
	} else {
		if (to.path == '/login') {
			next();
		} else {
			let url = escape(to.fullPath);
			router.push("/login?redirect=" + url);
		}
	}
});
router.afterEach((to,from) => {
	NProgress.done();
})

export default router


